import React, { Component } from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import { colors } from '../util/colors';
import { ComposableMap, Geographies, Geography, Markers, Marker, ZoomableGroup } from "react-simple-maps";
import { scaleLinear } from "d3-scale"

const Container = styled.div`
  padding-left: 20px;
  h3 {
    height: 10px;
  }
`;

const pinScale = scaleLinear()
  .domain([0, 1000000])
  .range([colors.color_coral_light, colors.color_coral]);

const orgScale = scaleLinear()
  .domain([0, 120])
  .range([3, 20]);


class MapContainer extends Component {
  static propTypes = {
    geoData: PropTypes.object,
    regionData: PropTypes.array,
    handleRegionSelect: PropTypes.func,
  };

  constructor(props) {
    super(props);
    this.state = { currentRegion: '' };
  }

  handleClick(geography, evt) {
    //console.log("Geography data: ", geography);
    this.props.handleRegionSelect(geography);
  }

  handleMouseEnter(geography, evt) {
    //console.log("Geography data: ", geography);
    this.setState({ currentRegion: geography.properties.admin1Name })
  }

  handleMouseLeave(geography, evt) {
    //console.log("Geography data: ", geography);
    this.setState({ currentRegion: '' })
  }

  render() {
    return (
      <Container>
        <h3>{this.state.currentRegion}</h3>
        <ComposableMap projectionConfig={{ scale: 200 }} width={600} height={500}>
          <ZoomableGroup zoom={10} center={[-4.5, 17.7]}>
            <Geographies geography={this.props.geoData}>
              {(geographies, projection) => geographies.map((geography,index) => (
                <Geography
                  key={ `geography-${index}` }
                  geography={ geography }
                  projection={ projection }
                  style={{
                    default: {
                      fill: pinScale(geography.properties.pin),
                      stroke: '#999',
                      strokeWidth: 0.1,
                      outline: 'none',
                    },
                    hover: {
                      fill: pinScale(geography.properties.pin),
                      stroke: '#999',
                      strokeWidth: 0.1,
                      outline: 'none',
                    },
                    pressed: {
                      fill: pinScale(geography.properties.pin),
                      stroke: '#999',
                      strokeWidth: 0.1,
                      outline: 'none',
                    }
                  }}
                  onMouseEnter={this.handleMouseEnter.bind(this)}
                  onMouseLeave={this.handleMouseLeave.bind(this)}
                  onClick={this.handleClick.bind(this)}
                  />
              ))}
            </Geographies>
            <Markers>
              {
                this.props.regionData.map((region, i) => (
                  <Marker key={i} marker={region}>
                    <circle
                      cx={0}
                      cy={0}
                      r={orgScale(region.orgs)}
                      fill="rgba(0,124,224,0.5)"
                      stroke="#007CE0"
                      strokeWidth="2"
                    />
                  </Marker>
                ))
              }
            </Markers>
          </ZoomableGroup>
        </ComposableMap>
      </Container>
    )
  }
}

export default MapContainer;
